* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scrollbar-width: thin;
  scrollbar-color: silver transparent;
  scrollbar-radius: 20px;
}

*::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: silver;
  border-radius: 20px;
}

html,body {
  background: url(https://ik.imagekit.io/7ol7k87qcca/movies/movies_e1GC0VBxF.jpg) center center fixed;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.overlay {
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.9);
}

.topbar {
  position: absolute;
  top: 5%;
  width: 95%;
  height: 50px;
  left: 0;
  right: 0;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header {
  font-size: 6vh;
  color: cornflowerblue;
  font-family: 'Poiret One', cursive;
  font-weight: lighter;
}

.searchbox {
  width: 400px;
  height: 100%;
  background: transparent;
  color: white;
  border-radius: 10px;
  border: 1px solid cornflowerblue;
  font-size: 30px;
  padding: 20px;
  outline: none;
}

.container {
  position: absolute;
  top: 15%;
  width: 100%;
  height: 400px;
  display: flex;
  align-items: center;
}

.collection {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  overflow-x: auto;
  overflow: hidden;
  scroll-behavior: smooth;
}

.movie {
  position: relative;
  width: 100%;
  height: 80%;
  margin-left: 10px;
}

.poster {
  height: 100%;
}

.fav {
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  width: 0;
  height: 100%;
  opacity: 0;
  color: white;
  text-align: center;
  transition: .3s ease;
}

.movie:hover > .fav {
  opacity: 1;
  width: 100%;
}

header {
  font-family: arial;
  color: white;
  position: absolute;
  top: 0;
  font-size: 35px;
  margin-left: 10px;
}

.left {
  background: rgba(0, 0, 0, 0.3);
  color: cornflowerblue;
  position: fixed;
  font-size: 50px;
  z-index: 100;
  height: 300px;
  width: 3%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .3s ease;
  cursor: pointer;
  user-select: none;
}

.right {
  background: rgba(0, 0, 0, 0.3);
  color: cornflowerblue;
  position: fixed;
  right: 0;
  font-size: 50px;
  z-index: 100;
  height: 300px;
  width: 3%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .3s ease;
  cursor: pointer;
  user-select: none;
}

.left:hover,
.right:hover {
  background: rgba(0, 0, 0, 0.9);
}
